*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  font-family: inherit; 
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth; 
}

body {
  box-sizing: border-box;
  position: relative;
  line-height: 1.5;
  font-family: sans-serif;
  overflow-x: hidden;
  overflow-y: scroll;
  font-family: 'Quicksand', sans-serif; 
}

a {
  text-decoration: none;
  color: inherit; 
}

li {
  list-style: none; 
}


@media only screen and (max-width: 75em) {
  html {
    font-size: 59%; } 
}

@media only screen and (max-width: 56.25em) {
html {
  font-size: 56%; } 
}

@media only screen and (min-width: 112.5em) {
html {
  font-size: 65%; } 
}